<template>
  <div id="app" class="flex items-center justify-center h-screen bg-gray-100">
    <div class="text-center">
      <h1 class="text-3xl font-bold mb-6">幸运转盘</h1>
      <div class="relative flex justify-center">
        <!-- 转盘画布 -->
        <canvas id="wheel" width="400" height="400" class="border-4 border-gray-300 rounded-full"></canvas>
        <!-- 指针 -->
        <div
          class="point absolute top-0 w-0 h-0 border-l-15 border-r-15 border-b-30 border-l-transparent border-r-transparent border-b-red-500 transform -translate-y-2"></div>
      </div>
      <button
        @click="spin"
        :disabled="spinning"
        class="mt-6 px-6 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:bg-gray-400 disabled:cursor-not-allowed"
      >
        {{ spinning ? '抽奖中...' : '开始抽奖' }}
      </button>
      <p v-if="result" class="mt-4 text-xl font-semibold text-green-600">{{ result }}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'LuckyWheel',
  data() {
    return {
      spinning: false,
      result: '',
      prizes: [
        { name: '一等奖', color: '#FF6B6B' },
        { name: '二等奖', color: '#4ECDC4' },
        { name: '三等奖', color: '#45B7D1' },
        { name: '谢谢参与', color: '#96CEB4' },
        { name: '四等奖', color: '#FFEEAD' },
        { name: '五等奖', color: '#D4A5A5' }
      ],
      startAngle: 0,
      spinTime: 0,
      spinTimeTotal: 0
    }
  },
  mounted() {
    window.document.title = '幸运转盘'
    this.drawWheel()
  },
  methods: {
    drawWheel() {
      const canvas = document.getElementById('wheel')
      const ctx = canvas.getContext('2d')
      const arc = Math.PI / (this.prizes.length / 2)
      ctx.clearRect(0, 0, canvas.width, canvas.height)

      for (let i = 0; i < this.prizes.length; i++) {
        const angle = this.startAngle + i * arc
        ctx.fillStyle = this.prizes[i].color
        ctx.beginPath()
        ctx.arc(200, 200, 200, angle, angle + arc, false)
        ctx.arc(200, 200, 0, angle + arc, angle, true)
        ctx.fill()
        ctx.save()
        ctx.fillStyle = '#FFFFFF'
        ctx.font = 'bold 18px Arial'
        ctx.translate(
          200 + Math.cos(angle + arc / 2) * 150,
          200 + Math.sin(angle + arc / 2) * 150
        )
        ctx.rotate(angle + arc / 2 + Math.PI / 2)
        ctx.fillText(this.prizes[i].name, -ctx.measureText(this.prizes[i].name).width / 2, 0)
        ctx.restore()
      }
    },
    spin() {
      if (this.spinning) return
      this.spinning = true
      this.result = ''
      this.spinTime = 0
      this.spinTimeTotal = Math.random() * 3000 + 4000 // 旋转4-7秒
      this.rotateWheel()
    },
    rotateWheel() {
      this.spinTime += 30
      if (this.spinTime >= this.spinTimeTotal) {
        this.stopRotateWheel()
        return
      }
      const spinAngle = this.easeOut(this.spinTime, 0, Math.random() * 360 + 720, this.spinTimeTotal)
      this.startAngle += (spinAngle * Math.PI / 180)
      this.drawWheel()
      requestAnimationFrame(this.rotateWheel.bind(this))
    },
    stopRotateWheel() {
      this.spinning = false
      const degrees = this.startAngle * 180 / Math.PI + 90
      const arcd = 360 / this.prizes.length
      const index = Math.floor((360 - degrees % 360) / arcd)
      this.result = `恭喜你获得：${this.prizes[index].name}!`
    },
    easeOut(t, b, c, d) {
      const ts = (t /= d) * t
      const tc = ts * t
      return b + c * (tc + -3 * ts + 3 * t)
    }
  }
}
</script>
